import { useEffect, useState, Fragment, useRef } from 'react'

import { PlusCircleIcon } from '@heroicons/react/24/outline'
import { useForm } from "react-hook-form";
import pricingFunctions from '../pricing/engines'
import Modal from '../../components/modal'
import { createOrUpdateWallet } from '../../functions'

const convertValueToUSD = async ({symbol}) => {
  return await pricingFunctions[symbol](symbol)
}

export default ({user, walletList, currencyList=[]}) => {
  const { register, handleSubmit, reset, formState: { isDirty } } = useForm();

  const [currencies, setCurrencies] = useState({})
  const [wallets, setWallets] = useState([])
  const [open, setOpen] = useState(false)
  const [updateWallet, setUpdateWallet] = useState({})
  const cancelButtonRef = useRef(null)

  useEffect(() => setWallets(walletList), [walletList])

  const onSubmit = data => {
    if (isDirty) {
      createOrUpdateWallet(data, user)
        .then(() => {
          setOpen(false)
        })
        .catch(console.error)
    }else{
      setOpen(false)
    }
  }

  const updateWalletOnClick = (wallet) => {
    setUpdateWallet(wallet)
    setOpen(true)
  }

  useEffect(() => {
    reset(updateWallet)
  }, [updateWallet])

  useEffect(() => {
    const currencies = currencyList.map((i)=>({symbol: i, usdEquivalent: '0'}));
    const updateCurrencies = async () => {
      const updatedCurrencies = await Promise.all(currencies.map(async (currency) => {
        const usdEquivalent = await convertValueToUSD(currency)
        return { ...currency, usdEquivalent }
      }))
      setCurrencies(updatedCurrencies)
    }
    updateCurrencies()
  }, [currencyList])

  return (
    <div className='p-4'>
      <div className='text-lg pb-4 flex'>
        Wallets <PlusCircleIcon onClick={()=>setOpen(true)} className='h-6 w-6 opacity-50 hover:opacity-100 cursor-pointer' />
      </div>
      <div>
        <div className='grid grid-cols-2 gap-3'>
          {wallets.map((item, index) => (
            <div onClick={()=>updateWalletOnClick(item)} key={`${item.name}_${item.currency}_${item.amount}_${index}`} className='hover:bg-gray-100 cursor-pointer ring-2 ring-black overflow-hidden bg-white px-4 py-5 shadow sm:p-6'>
              <div className='truncate text-sm font-medium text-gray-500'>{item.name || 'unkown'}</div>
              <div className='mt-1 text-xl font-semibold tracking-tight text-gray-900'>{item.currency} {item.amount}</div>
            </div>
          ))}
        </div>
      </div>
      <Modal 
        open={open}
        setOpen={setOpen}
        cancelButtonRef={cancelButtonRef}
        title='Wallet'
      >
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className='flex flex-col'>
          <input type="text" placeholder='bank name' className="my-1 p-2 border border-gray-300 rounded-md shadow-sm focus:ring-gray-500 focus:border-gray-500" {...register("name", { required: true })} />
          <input type="number" step="any" placeholder='amount' className="my-1 p-2 border border-gray-300 rounded-md shadow-sm focus:ring-gray-500 focus:border-gray-500" {...register("amount", { required: true })} />
          <input type="text" placeholder='currency' className="my-1 p-2 border border-gray-300 rounded-md shadow-sm focus:ring-gray-500 focus:border-gray-500" {...register("currency", { required: true })} />
        </div>
        <div className="mt-5 sm:mt-4 sm:flex sm:flex-row-reverse">
          <button
            type="submit"
            className="inline-flex w-full justify-center rounded-md border border-transparent bg-gray-600 px-4 py-2 text-base font-medium text-white shadow-sm hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-gray-500 focus:ring-offset-2 sm:ml-3 sm:w-auto sm:text-sm"
            >
            Save
          </button>
        </div>
        </form>
      </Modal>
    </div>
  )
}
