import { createRoot } from 'react-dom/client';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Dashboard from './pages/dashboard';
import { useForm } from "react-hook-form";
import { UserCircleIcon } from '@heroicons/react/24/outline'
import { useEffect, useState, Fragment, useRef } from 'react'
import Modal from './components/modal'
import './style.scss';
import { auth } from './functions'

const App = () => {
  const [currentUser, setCurrentUser] = useState(null)
  const [currentUserId, setCurrentUserId] = useState(null)
  const [open, setOpen] = useState(false)
  const cancelButtonRef = useRef(null)
  const { register, handleSubmit } = useForm();

  const onAuth = ({email}) => {
    auth(email)
      .then((res) => {
        const {email, _id} = res.data
        localStorage.setItem('portfoliooo_email', email)
        localStorage.setItem('portfoliooo_uuid', _id)
        setCurrentUser(email)
        setCurrentUserId(_id)
        setOpen(false)
      })
  }

  useEffect(() => {
    setCurrentUser(localStorage.getItem('portfoliooo_email'))
    setCurrentUserId(localStorage.getItem('portfoliooo_uuid'))
  }, [])

  const logOut = () => {
    localStorage.removeItem('portfoliooo_email')
    localStorage.removeItem('portfoliooo_uuid')
    setCurrentUser(null)
    setCurrentUserId(null)
    setOpen(false)
  }

  return (
    <BrowserRouter>
      <Fragment>
        <div className='float-right'>
          {currentUser && <div onClick={()=>setOpen(true)} className='flex align-middle rounded-full bg-black text-white text-xs px-3 p-2 m-2 h-8 opacity-100 cursor-pointer'>{currentUser}</div>}
          {!currentUser && <UserCircleIcon onClick={()=>setOpen(true)} className='rounded hover:bg-gray-100 active:p-3 p-2 m-2 h-10 w-10 opacity-50 hover:opacity-100 cursor-pointer' />}
        </div>
        <Modal 
            open={open}
            setOpen={setOpen}
            cancelButtonRef={cancelButtonRef}
            title='Authentication'
          >
          {!currentUser && <form onSubmit={handleSubmit(onAuth)}>
            <div className='flex flex-col'>
              <input type="email" placeholder='email' className="my-1 p-2 border border-gray-300 rounded-md shadow-sm focus:ring-gray-500 focus:border-gray-500" {...register("email", { required: true })} />
            </div>
            <div className="mt-5 sm:mt-4 sm:flex sm:flex-row-reverse">
              <button
                type="submit"
                className="inline-flex w-full justify-center rounded-md border border-transparent bg-gray-600 px-4 py-2 text-base font-medium text-white shadow-sm hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-gray-500 focus:ring-offset-2 sm:ml-3 sm:w-auto sm:text-sm"
                >
                Login
              </button>
            </div>
            </form>}

            {currentUser && <Fragment>
              <div className='mt-2'>{currentUser}</div>
              <div className="mt-5 sm:mt-4 sm:flex sm:flex-row-reverse">
              <button
              className="inline-flex w-full justify-center rounded-md border border-transparent opacity-70 hover:opacity-100 bg-red-600 px-4 py-2 text-base font-medium text-white shadow-sm hover:bg-red-700 focus:outline-none ring-0 focus:ring-red-500 focus:ring-offset-2 sm:ml-3 sm:w-auto sm:text-sm"
              ref={cancelButtonRef}
              onClick={() => logOut()}
              >
              Logout
            </button>  
            </div>
            </Fragment>}
          </Modal>
      </Fragment>
      <Routes>
        <Route path="/" element={<Dashboard user={currentUserId} />} />
        <Route path="/dashboard" element={<Dashboard user={currentUserId} />} />
      </Routes>
    </BrowserRouter>
  );
}

const root = createRoot(document.getElementById('root'));
root.render(<App />);
