import axios from 'axios'
import config from '../config'

axios.defaults.baseURL = config.apiEngpoint

const authHeader = (userId) => ({
  headers: {
    userid: userId
  }
})

export const auth = (email) => axios.post('/user', { email })
export const getUserDashboard = (userId) => axios.get(`/user/dashboard/${userId}`)
export const createOrUpdateWallet = (wallet, user) => axios.post(`/wallet/${wallet._id}`, wallet, authHeader(user))
