import { useEffect, useState } from 'react';
import _, { omit } from 'lodash'

import {getUserDashboard} from '../../functions'
import Pricing from "../pricing"
import Wallets from "../wallets"

export default ({user}) => {
  const [wallets, setWallets] = useState([])
  const [currencies, setCurrencies] = useState([])
  
  useEffect(() => {
    if(user) {
      getUserDashboard(user).then(({data}) => {
        setWallets(data.wallets)
        setCurrencies(_.uniq(data.currencies))
      })
    }
  }, [user])

  return <div>
    <Pricing currencyList={currencies} />
    <hr />
    <Wallets user={user} currencyList={currencies} walletList={wallets} />
  </div>
}
