import axios from 'axios'

const fiatToUsd = async (symbol) => {
  const response = await axios.get(`https://api.exchangerate.host/convert?from=USD&to=${symbol}`)
  return response.data.info.rate
}

const cryptoToUsd = async (symbol) => {
  const response = await axios.get(`https://api2.binance.com/api/v3/ticker/price?symbol=${symbol.toUpperCase()}USDT`)
  return Number(response.data.price)
}

const pricingFunctionsRegistry = {
  USD: fiatToUsd,
  GBP: fiatToUsd,
  EUR: fiatToUsd,
  TWD: fiatToUsd,
  BNB: cryptoToUsd,
  ETH: cryptoToUsd,
  CRO: ()=>0.07,
  DOGE: cryptoToUsd,
  EOS: cryptoToUsd,
  VTB: ()=>8.9999
}

export default pricingFunctionsRegistry;