import { useEffect, useState } from 'react'

import pricingFunctions from './engines'

const convertValueToUSD = async ({symbol}) => {
  return await pricingFunctions[symbol](symbol)
}

export default ({currencyList=[]}) => {
  const [currencies, setCurrencies] = useState([])

  useEffect(() => {
    const currencies = currencyList.map((i)=>({symbol: i, usdEquivalent: '0'}));
    const updateCurrencies = async () => {
      const updatedCurrencies = await Promise.all(currencies.map(async (currency) => {
        const usdEquivalent = await convertValueToUSD(currency)
        return { ...currency, usdEquivalent }
      }))
      setCurrencies(updatedCurrencies)
    }
    updateCurrencies()
  }, [currencyList])

  return (
    <div className='p-4'>
      <div className='text-lg pb-4 flex'>
        Currencies
      </div>
      <div>
        <div className='grid grid-cols-2 gap-3 xs:grid-cols-4 sm:grid-cols-3 md:grid-cols-4 xl:grid-cols-6'>
          {currencies.map((item) => (
            <div key={item.symbol} className='ring-2 ring-black overflow-hidden bg-white px-4 py-5 shadow sm:p-6'>
              <div className='truncate text-sm font-medium text-gray-500'>{item.symbol}</div>
              <div className='mt-1 text-3xl font-semibold tracking-tight text-gray-900'>${item.usdEquivalent}</div>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}
